// Selected DOM elements
const menuToggle = document.querySelector('.menu-toggle');
const menuIcon = document.querySelector('.icon-menu');
const siteMenu = document.querySelector('.site-menu');
const socialMenu = document.querySelector('.social-menu');

// Site and social menu toggle
if (menuToggle) {
  menuToggle.addEventListener('click', () => {
    siteMenu.classList.toggle('collapsed');
    socialMenu.classList.toggle('collapsed');
    menuIcon.classList.toggle('icon-menu');
    menuIcon.classList.toggle('icon-close');
  });
}

const unhiders = document.querySelectorAll('.unhide');
for (let i = 0; i < unhiders.length; i += 1) {
  const elt = unhiders[i];
  elt.addEventListener('click', () => {
    const target = document.getElementById(elt.dataset.unhide);
    if (target.classList.contains('hidden')) {
      target.classList.remove('hidden');
      elt.dataset.reshow = elt.innerText;
      elt.innerText = elt.dataset.rehide;
    } else {
      target.classList.add('hidden');
      elt.innerText = elt.dataset.reshow;
    }
  });
}

if (!(navigator && navigator.doNotTrack === '1')) {
  const request = new XMLHttpRequest();
  request.open('GET', `https://node.vero.site/count/${encodeURIComponent(window.location)}`, true);
  request.send();
}
